.admin-view {
  padding: var(--size3);
  display: flex;
  flex-direction: column;
  gap: var(--size3);
}

.teachers {
  display: flex;
  flex-direction: column;
  gap: var(--size2);
}

.teacher {
  display: flex;
  justify-content: space-between; 
  background-color: var(--second-main);
  padding: var(--size2) var(--size3);
  color: var(--main-white);
  border-radius: var(--border);
}

.check-btn {
  background: none;
  border: none;
  display: flex;
  height: fit-content;
  align-self: center;
  align-items: center;
  gap: var(--size1);
}

.check-btn p {
  display: contents;
  font-weight: bold;
  color: var(--main-white);
}