.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(50, 54, 65, 0.85);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(2px);
}

.container {
  position: fixed;
  left: 50%;
  top: 50%;
  transform:  translate(-50%,-50%) scale(1);
  z-index: 30;
  background-color: var(--second-main);
  /* padding: var(--size3); */
  border-radius: var(--border);
  box-shadow: var(--modal-shadow);
  min-width: 80%;
  animation: appear-modal .3s;
}

.cancel-btn {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  position: absolute;
  top: var(--size2);
  right: var(--size2);
}

@keyframes appear-modal {
  0% {
    transform: translate(-50%,-80%) scale(1);
  }
  100% {
    transform: translate(-50%,-50%) scale(1);
  }
}